#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  margin-top: 60px;
}

li {
  list-style-type: none;
}

.el-input {
    max-width: 300px;
    margin-right: 10px;
}
.el-button {
    margin-top: 10px;
}

